import React, { Component } from 'react';
import Mentors from '../../../data/Mentors';
import './styles.css';

class TableInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  renderList = () => {
    return Mentors.filter(mentor => {
      if (
        (mentor.fullname.toUpperCase().includes(this.props.valueMentor.toUpperCase()) ||
          mentor.githubName[0].toUpperCase().includes(this.props.valueMentor.toUpperCase())) &&
        this.props.valueMentor !== ''
      ) {
        return true;
      }
      return false;
    });
  };
  render() {
    return (
      <div className="list-group">
        <button
          type="button"
          key={this.props.setHelpItem}
          className="list-group-item list-group-item-action"
          disabled
        >
          Первые 6 совпадений:
        </button>
        {this.renderList().map((e, i) => {
          return i < 6 ? (
            <button
              type="button"
              className="list-group-item list-group-item-action"
              key={e.fullname + i}
              onClick={this.props.setHelpItem}
              value={e.fullname}
            >
              {e.fullname + ', '}
              <a href={e.github} key={e.github}>
                {e.githubName[0]}
              </a>
            </button>
          ) : null;
        })}
      </div>
    );
  }
}

export default TableInfo;
