import React, { Component } from 'react';
import Thead from '../Thead';
import Tbody from '../Tbody';
import TableB from 'react-bootstrap/Table';

class Table extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="table">
        <TableB bordered responsive size="sm">
          <Thead valueMentor={this.props.valueMentor} mentorIndex={this.props.mentorIndex} />
          <Tbody valueMentor={this.props.valueMentor} mentorIndex={this.props.mentorIndex} />
        </TableB>
      </div>
    );
  }
}

export default Table;
