import React from 'react';
import Popover from 'react-bootstrap/Popover';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Button from 'react-bootstrap/Button';

function TaskInfo(props) {
  let getScoreRangeColor = score => {
    if (parseInt(score, 10) < 50) {
      if (parseInt(score, 10) < 20) return 'text-danger';
      else return 'text-warning';
    } else return 'text-success';
  };
  const popover = (
    <Popover id="popover-basic" title={props.taskInfo.name}>
      <p>{props.taskInfo.note}</p>
      <h6 className={getScoreRangeColor(props.taskInfo.score)}>Баллы: {props.taskInfo.score}</h6>
      <a href={props.taskInfo.pr}>{props.taskInfo.pr}</a>
    </Popover>
  );

  const Example = () => (
    <OverlayTrigger trigger="click" placement="auto" overlay={popover}>
      <Button variant="success">{props.taskInfo.score}</Button>
    </OverlayTrigger>
  );
  return <Example />;
}

export default TaskInfo;
