import React from 'react';
import Mentors from '../../../data/Mentors';
import './styles.css';
import TaskInfo from './TaskInfo';

function Thead(props) {
  function getStatusClass(status) {
    switch (status) {
      case 'Checked':
        return 'bg-success';
      case 'Checking':
        return 'bg-danger';
      case 'In Progress':
        return 'bg-warning';
      case 'ToDo':
        return 'bg-secondary';
      default:
        return null;
    }
  }
  function getStatusClassForNotCheckedTasks(status) {
    switch (status) {
      case 'Checked':
        return 'bg-danger';
      case 'Checking':
        return 'bg-warning';
      case 'ToDo':
        return 'bg-secondary';
      default:
        return null;
    }
  }
  const renderStudentTaskInfo = student => {
    return student.tasks.map(task => {
      if (task.name.toUpperCase() === props.taskName.toUpperCase()) {
        return (
          <td className="text-center bg-success" key={task.name + student.name}>
            <TaskInfo key={task.name + student.name + task.score} taskInfo={task} />
          </td>
        );
      } else {
        return null;
      }
    });
  };
  const renderStudentTasks = props.mentorIndex
    ? Mentors[props.mentorIndex].students.map(student => {
        return student.tasks.some(
          task => task.name.toUpperCase() === props.taskName.toUpperCase()
        ) ? (
          renderStudentTaskInfo(student)
        ) : (
          <td className={getStatusClassForNotCheckedTasks(props.taskStatus) + ' text-center'}>-</td>
        );
      })
    : null;

  if (props.mentorIndex)
    return (
      <tr key={props.taskName}>
        <td className={getStatusClass(props.taskStatus)}>{props.taskName}</td>
        {renderStudentTasks}
      </tr>
    );
  else return null;
}

export default Thead;
