import React, { Component } from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Search from '../Searsh';
import Table from '../Table';
import TableInfo from '../TableInfo';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      valueMentor: '',
      mentorIndex: '',
    };
  }
  handleMentorChange = (value, mentorIndex) => {
    this.setState(Object.assign(this.state, { valueMentor: value, mentorIndex: mentorIndex }));
  };

  render() {
    return (
      <div className="App bg-white">
        <header className="App-header">
          <Search onMentorChange={this.handleMentorChange} />
        </header>
        <div className="App-body">
          <Table valueMentor={this.state.valueMentor} mentorIndex={this.state.mentorIndex} />
        </div>
        <footer className="App-footer">
          <TableInfo className="App-footer__table" />
          <div className="App-footer__content">Vlad Semenik for mentor Kiryl Shaliapin</div>
        </footer>
      </div>
    );
  }
}

export default App;
