import React, { Component } from 'react';
import Mentors from '../../data/Mentors';
import Students from './Students';

class Thead extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let header;
    if (this.props.mentorIndex && this.props.valueMentor)
      header = (
        <tr key={this.props.mentorIndex + Mentors[this.props.mentorIndex].name}>
          <th className="text-center" key="tasks">
            Tasks
          </th>
          <Students mentorId={this.props.mentorIndex} />
        </tr>
      );
    else header = null;
    return <thead className="thead">{header}</thead>;
  }
}

export default Thead;
