import React from 'react';
import TasksInfo from '../../data/TasksInfo';
import Task from './Task';

function Tbody(props) {
  const renderTaskInfo = TasksInfo.map(task => {
    return (
      <Task
        taskName={task.name}
        taskStatus={task.status}
        mentorIndex={props.mentorIndex}
        key={task.name}
      />
    );
  });
  return <tbody className="tbody">{renderTaskInfo}</tbody>;
}

export default Tbody;
