import React, { Component } from 'react';
import Mentors from '../../data/Mentors';
import HelpList from './HelpList';

class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      valueMentor: '',
    };
  }
  handleSearchValue = event => {
    let that = event.target;
    this.setState((state, props) => {
      return { valueMentor: that.value };
    });
    this.props.onMentorChange(event.target.value, this.getMentorIndex(event.target.value));
  };
  getMentorIndex = name => {
    let id = null;
    name = name.toUpperCase();
    Mentors.forEach((mentor, i) => {
      if (
        (name.includes(mentor.name.toUpperCase()) && name.includes(mentor.surname.toUpperCase())) ||
        (name === mentor.githubName[0].toUpperCase() && name !== '')
      ) {
        id = i;
      }
      return null;
    });
    return id;
  };
  setInputValue = event => {
    document.querySelector('.search-input').value = event.target.value;
    let that = event.target;
    this.setState((state, props) => {
      return { valueMentor: that.value };
    });
    this.handleSearchValue(event);
  };
  render() {
    return (
      <div className="search">
        <label className="search-label" htmlFor="search-label">
          Имя ментора:{' '}
        </label>
        <div className="search-content">
          <input
            className="search-input form-control"
            onChange={this.handleSearchValue}
            type="text"
            id="search-label"
            placeholder="Введите имя или github"
            size="22"
            autoComplete="off"
          />
          {this.state.valueMentor ? (
            <HelpList
              className="position-relative"
              valueMentor={this.state.valueMentor}
              setHelpItem={this.setInputValue}
            />
          ) : null}
        </div>
      </div>
    );
  }
}

export default Search;
